import React, { Component } from 'react'
import fetchUtil from 'utils/fetchUtil'
import { getUrlParam } from 'utils/tools'
import { fixDomain } from 'utils/domain'
// import { Link } from 'react-router'
import './index.scss'

class H5Header extends Component {
  state = {
    headerShow: true
  }

  componentDidMount() {
    // const apiUrl = fixDomain('http://api.sit.ffan.net/bobweb/v1/user/basic')
    const actUrl = fixDomain('http://open.sit.ffan.net/store/home')
    const loginUrl = fixDomain('http://open.sit.ffan.net/store/login?backUrl=')
    const tokenCookie = document.cookie.toUpperCase().includes('TOKEN')
    const tokenUrl = getUrlParam('TOKEN')
    if (!tokenCookie && !location.hostname.includes('localhost') && !tokenUrl) {
      location.href = `${loginUrl}${actUrl}`
    }
    if (tokenUrl) {
      this.setState({
        headerShow: false,
      })
    }
    //个人中心如果显示名称
    // fetchUtil.getJSON(apiUrl).then(res => {
    //   this.setState({
    //     personInfo: res.userName
    //   })
    // }).catch(e => {
    //   console.log(e)
    // })
  }

  logout() {
    const apiUrl = fixDomain('http://api.ffan.net/mapp/v1/loginout')
    const actUrl = fixDomain('http://open.sit.ffan.net/store/home')
    const loginUrl = fixDomain('http://open.sit.ffan.net/store/login?backUrl=')
    fetchUtil.getJSON(apiUrl).then(res => {
      location.href = `${loginUrl}${actUrl}`
    }).catch(e => {
      console.log(e)
    })
  }

  render() {
    return <div className="h5-fs-header">
      <a className="logo" href="/store/home">
        <h1 className="title">
          <img src="http://timg.ffan.com/convert/resize/url_T149LTBsJ_1RCvBVdK/tfs/1.png" />
          <span className="text">BO商户平台</span>
        </h1>
      </a>
      <p className="title">{document.title || '门店装修'}</p>
      <div className="login">
        <a href="/fe/static/store-personalCenter.html">个人中心</a>
        <span>|</span>
        <a className="logout" onClick={this.logout}>退出</a>
      </div>
    </div>
  }
}

export default H5Header



