import React from 'react'
import { Form, Modal, Input } from 'antd'
const FormItem = Form.Item

const WeChatCreateForm = Form.create()(
  (props) => {
    const { visible, onCreate, onCancel, form, ...rest } = props
    const { getFieldDecorator } = form
    return (
      <div>
        <Modal
          visible={visible}
          title="请设置店铺配置名称"
          closable={false}
          onOk={onCreate}
          onCancel={onCancel}
        >
          <Form layout="horizontal" >
            <FormItem label={'配置名称'}>
              {getFieldDecorator('pageName', {
                initialValue: rest.pageName || '金地中心广场'
              })(< Input />)}
            </FormItem>
            <FormItem label={'预览图'}>
              {getFieldDecorator('pageLogo', {
                initialValue: rest.pageLogo || 'http://timg.ffan.com/convert/resize/url_T1IuDTBCLs1RCvBVdK/tfs/1.png'
              })(< Input />)}
            </FormItem>
          </Form>
        </Modal>
      </div>
    )
  }
)

export default WeChatCreateForm



