import React from 'react'
import Header from '../../routes/H5Editor/components/H5Header'
import './index.scss'

const App = ({ tplData, active, ...rest }) => {
  return (
    <div>
      <Header />
      <div className="t-container">
        <ul className="phone-box">
          <li className="preview-defaultPage" onClick={e => rest.handleCreate()}>
            <i className="iconfont icon-add" />
            <span>创建新店铺</span>
          </li>
          {
            tplData.length ? tplData.map((item, index) => {
              return <li
                onMouseEnter={e => rest.fetchQRCode(item.pageId, index)}
                onMouseLeave={e => rest.removeQRCode()}
              >
                <div className="control-list">
                  <a onClick={(e) => rest.edit(item.pageId, index)}><i className="iconfont icon-bianji" /></a>
                  <a onClick={(e) => rest.showModal(item.pageId, index)}><i className="iconfont icon-delete" /></a>
                </div>
                <a href={`/merchant/h5/edit/${item.pageId}?clientType=6`}>
                  <div className="preview-img">
                    {rest.index === index && rest.qrCodeArr[index] ? <div className={`${active ? 'active' : 'normal'}`}>
                      <img src={rest.qrCodeArr[index]} />
                      <div className="preview-des">
                        <p>扫码预览</p>
                        <p>{item.pageName}</p>
                      </div>
                    </div> : ''}
                    {item.pageLogo
                      ? <img src={item.pageLogo} />
                      : <img src={'http://timg.ffan.com/convert/resize/url_T1GfbTB_Ev1RCvBVdK/tfs/1.png'} />
                    }
                  </div>
                </a>
              </li>
            }) : null
          }
        </ul>
      </div>
    </div>
  )
}
export default App



